:root {
  --OldGray1:#F9F9F9;
  --OldGray2:#F5F5F5;
  --OldGray3:#E9ECEF;
  --OldGray4:#EEEEEE;
  --Gray5:#CCCCCC;
  --OldGray6:#AAAAAA;
  --OldGray7:#777777;
  --OldGray9:#333333;
  --Red1:#FF0000;
  --Blue:#0078FF;
}

html {
  font-size: 10px;
  line-height: 10px;
}

html[data-useragent*=iphone] input, html[data-useragent*=iphone] textarea {
  outline: 0;
}

body, button, code, input, select, table, textarea {
  font-family: -apple-system, BlinkMacSystemFont, helvetica, Apple SD Gothic Neo, sans-serif;
  font-size: 16px;
  line-height: 20px;
}

article, audio, body, button, dd, details, dl, dt, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, input, legend, li, menu, nav, ol, p, section, table, td, textarea, th, ul, video {
  margin: 0;
  padding: 0;
}

fieldset, img {
  border: 0;
}

img {
  vertical-align: middle;
}

ol, ul {
  list-style: none;
}

address, cite, dfn, em, i {
  font-style: normal;
}

table {
  border-collapse: collapse;
}

table caption {
  display: none !important;
}

a {
  text-decoration: none;
  color: inherit;
}

button, input {
  border-radius: 0;
  border: 0;
}

button {
  background-color: transparent;
}

.blind {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0, 0, 0, 0);
}

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: calc(env(safe-area-inset-right) + 0px);
  left: calc(env(safe-area-inset-left) + 0px);
  height: 50px;
  background: #fff;
  z-index: 10;
}
.header .m_logo {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 95px;
  height: 16px;
  background-position: 0px 0px;
}
.header .m_logo a {
  display: block;
  height: 100%;
}
.header .header_left,
.header .header_right {
  position: absolute;
  font-size: 0;
}
.header .header_left {
  left: 11px;
}
.header .header_right {
  right: 11px;
}
.header [class^=btn] {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  font-size: 13px;
  vertical-align: middle;
}
.header [class^=btn]:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.header [class^=btn] + [class^=btn] {
  margin-left: 10px;
}
.header .btn_alarm:before {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 20px;
  height: 23px;
  background-position: 0px -18px;
}
.header .btn_alarm .alarm_new {
  position: absolute;
  top: 0;
  right: 0;
  width: 4px;
  height: 4px;
  background: var(--Blue);
  border-radius: 50%;
}
.header .btn_like:before {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 23px;
  height: 20px;
  background-position: -22px -18px;
}
.header .btn_cart:before {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 18px;
  height: 23.5px;
  background-position: 0px -43px;
}
.header .btn_cart .cart_counting {
  position: absolute;
  right: -3px;
  bottom: -3px;
  height: 18px;
  line-height: 18px;
  padding: 0 5px;
  border-radius: 9px;
  background: var(--Blue);
  font-size: 11px;
  color: #fff;
}

#container {
  padding: 50px env(safe-area-inset-right) 60px env(safe-area-inset-left);
}

.cm_link {
  display: block;
}

.cm_inner {
  padding: 0 15px;
}

.cm_section {
  clear: both;
  position: relative;
}

.cm_content_box {
  padding-top: 60px;
}
.cm_content_box:first-of-type {
  padding-top: 15px;
}

.cm_sub_info {
  margin-top: 20px;
  font-size: 12px;
  line-height: 18px;
  color: var(--OldGray6);
}
.cm_sub_info + .cm_rel_more {
  margin-top: 7px;
}

.cm_rel_more {
  text-align: center;
}
.cm_rel_more .cm_rel_btn {
  display: inline-flex;
  align-items: center;
  padding: 0 20px;
  height: 40px;
  vertical-align: top;
  border-radius: 20px;
  border: 1px solid var(--OldGray3);
  box-sizing: border-box;
  font-size: 14px;
  color: var(--OldGray7);
}
.cm_rel_more .cm_rel_btn.more:after {
  content: "";
  margin: 0 -7px 0 9px;
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 18px;
  height: 18px;
  background-position: -47px -18px;
}
.cm_rel_more .cm_rel_btn.refresh:before {
  content: "";
  margin: 0 4px 0 0;
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
  background: url("../img/sp_common.png") 0 0 no-repeat;
  background-size: 95px 66.5px;
  width: 18px;
  height: 18px;
  background-position: -67px -18px;
}